html, body {
  padding: 0px;
  margin: 0px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
  width: 100%;
}

.App {
  background-color: #300a24;
  color: #dfdbd2;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  border-radius: 2px;
  overflow-x: hidden;
  overflow-y: auto;
  width: 100%;
  height: 100%;
  max-height: 100vh;
  max-width: 100%;
}

.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  height: 25px;
  background-color: #3f3e3a;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 5px;
  font-size: 14px;
  z-index: 10;
}

.header-button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 12px;
  width: 12px;
  border-radius: 50%;
  background-color: #e55827;
  text-align: center;
  font-size: 12px;
  color: #34322e;
  padding: 0;
  margin: 0;
  margin-right: 1.5px;
  border: solid #393834 1px;
  overflow: hidden;
  cursor: pointer;
}

.header-button p {
  padding: 0;
  margin: 0;
  margin-top: -2px;
}

.header-button.minimize, .header-button.fullscreen {
  background-color: #666;
}

.header-button.minimize p {
  margin-top: -2px;
}

.header-button.fullscreen p {
  margin-top: -1.5px;
  margin-left: 1px;
}

.editable {
  outline: none;
  margin-left: 8px;
}

.editable * { display: inline }

.editable br {
  display: none
}

.command-item, div.command-item {
  width: 100%;
  word-break: break-all;
}



